@media only screen and (max-width: 400px) {
  .thumbnail {
    height: 90vw;
    width: 90vw;
    object-fit: cover;
  }
  .image-large h1 {
    font-size: 2 rem;
  }
}

@media only screen and (max-width: 600px) {
  .photo-medium {
    height: 50vh;
    width: 50vh;
    object-fit: cover;
    margin-bottom: 20px;
  }
}

.image-large h1 {
  margin-bottom: 20px;
}

.sandbox {
  overflow-y: hidden;
  overscroll-behavior: contain;
}
